import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			token: uni.getStorageSync('cdj_token'),
			imgRemote: imgRemote,
			navBar: navBar,
			shop: [],
			config: [],
			showRemark:false,
			confirmBar:false,
			remark:'',
			shopItem:{},
			firstIndex:'',
			secondIndex:'',
			ruleKind:-1,
			giftInfo:[]
		}
	},
	methods: {
		closeKey(){
			this.$refs.keyboard.cancel();
		},
		leftClick() {
			uni.navigateBack({
				delta: 1
			})
		},
		rightClick(){
			var that = this;
							uni.showModal({
								title: '',
								content: '确定清空购物车吗？',
								success(res) {
									if (res.confirm) {
										//获取清空的id
										let list = that.shop;
										let newlist = [];
										for (let i of list) {
											i.list.forEach(v => {
												newlist.push(v.cart_id)
											})}
											log(newlist)
											let timeStamp = Math.round(new Date().getTime() / 1000);
											let obj = {
												appid: appid,
												timeStamp: timeStamp
											};
											let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
											let params = Object.assign({
												sign: sign,
												keys: newlist
											}, obj)
											rs.postRequests("deleteByUserIds", params, (res) => {
												if (res.data.code == 200) {
													rs.Toast('成功清空购物车');
													setTimeout(()=>{uni.switchTab({
														url: '/pages/tabar/index'
													});},200)
													
												} else {
													rs.Toast(res.data.msg);
												}
											})
									}
								}
							});

		},
		deleteSign(data,one,two){
			let that=this;
			uni.showModal({
				content:'确定将该物品删除吗？',
				success(res){
					if(res.confirm){
						let id, attrid;
						if (data.attr.length == 0) {
													id = data.id;
													attrid = 0
												} else {
													id = data.attr.item_id;
													attrid = data.attr.id
												}
												let timeStamp = Math.round(new Date().getTime() / 1000);
												let obj = {
													appid: appid,
													timeStamp: timeStamp,
													item_id: id,
													attr_id: attrid
												};
												let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
												let params = Object.assign({
													sign: sign
												}, obj)
												rs.postRequests("deleteCart", params, (res) => {
													if (res.data.code == 200) {
														rs.Toast('成功删除该商品')
														that.shop[one].count--;
														that.shop[one].list.splice(two, 1);
														if (res.data.data.countNum == 0) {
															setTimeout(()=>{uni.switchTab({
																url: '/pages/tabar/index'
															});},200)
															
														}
													}
												})
					}
				}
			})
		},
		openCart() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign
			}, obj)
			rs.getRequests("openCart", params, (res) => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.config = data.data;
					this.shop = data.data.shop;
					if(data.data.activity_type==2){
						this.giftInfo=data.data.activity_rule;
							this.getCount();
					}
				}

			})
		},
		getFocus(item,index,second){
			// this.$nextTick(() =>{
			//     this.$refs.remarkTxt.focus();
			// })
	
			this.showRemark=true;
			this.remark=item.remark;
			this.shopItem=item;
			this.firstIndex=index;
			this.secondIndex=second;
		},
		determine(){
					let timeStamp = Math.round(new Date().getTime() / 1000);
						let obj = {
							appid: appid,
							timeStamp: timeStamp
						};
						let newobj = {};
						let item = this.shopItem;
							let itemId, attrId;
						if (item.attr.length != 0) {
							itemId = item.attr.item_id;
							attrId = item.attr.id;
						} else {
							itemId=item.id;
							attrId=0;	
						}
		            newobj = Object.assign({
			item_id: itemId,
			attr_id: attrId,
			remark: this.remark.replace(/\s+/g, ""),
		  }, obj);
						let sign = md5.hexMD5(rs.objKeySort(newobj) + appsecret);
						let params = Object.assign({
							sign: sign
						}, newobj);
						rs.postRequests("itemRemark", params, (res) => {
							if (res.data.code == 200) {
								rs.Toast('修改备注成功');
								this.shop[this.firstIndex].list[this.secondIndex].remark=this.remark;
								this.showRemark = false;
							} else {
								rs.Toast(res.data.msg)
							}
						})
		},
		showKey(data,one,two){
		this.$refs.popup.open();
		this.shopItem=data;
		this.firstIndex=one;
		this.secondIndex=two;
	},
	addCart(data, one, two,num) {
					let timeStamp = Math.round(new Date().getTime() / 1000);
					let obj = {
						appid: appid,
						timeStamp: timeStamp
					};
	
					let newobj = {};
					let item = data;
					let nums = num;
					let isFloat = "";
					let itemId, attrId;
					if (item.attr.length != 0) {
						isFloat = item.attr.is_float;
						itemId = item.attr.item_id;
						attrId = item.attr.id;
					} else {
						isFloat = item.is_float;
						itemId=item.id;
						attrId=0;
					}
					newobj = Object.assign({
						item_id: itemId,
						attr_id: attrId,
						item_num: num
					}, obj);
					if (isFloat == 1 && !Number.isInteger(Number(nums))) {
						rs.Toast('购买数量不能为小数');
						return;
					}
					let sign = md5.hexMD5(rs.objKeySort(newobj) + appsecret);
					let params = Object.assign({
						sign: sign
					}, newobj);
					if (num == 0) {
						rs.postRequests("deleteCart", params, (res) => {
							if (res.data.code == 200) {
								rs.Toast('删除成功');
							this.shop[one].count--;
							this.getCount();
							this.shop[one].list.splice(two, 1);
							if (res.data.data.countNum == 0) {
								setTimeout(()=>{uni.switchTab({
									url: '/pages/tabar/index'
								});},200)
							}
							} else {
								rs.Toast(res.data.msg)
							}
						})
					} else {
						rs.postRequests("changeNum", params, (res) => {
							let {data}=res;
							if (res.data.code == 200) {
								rs.Toast('操作成功');
								this.$refs.popup.close();
								this.shop[one].list[two].cart_num=num;
								this.getCount();
							}else if(data.code==406||data.code==407){
								rs.Toast('超出活动数量，将恢复原价');
									if(item.attr.length==0){
											this.shop[one].list[two].cart_num=item.activity_num;
									}else{
											this.shop[one].list[two].cart_num=item.attr.activity_num;
									}
								this.$refs.popup.close();
							}else{
								rs.Toast(data.msg)
							}
						})
					}
				},
	minus(item,one,two,num){
			this.addCart(item,one, two,num)
	},
	plus(item,one,two,num){	
		this.addCart(item, one, two,num);
		},
	toParent(e){
	this.addCart(e.arrObj, this.firstIndex, this.secondIndex,e.val);
	},
	getCount(){
		let timeStamp = Math.round(new Date().getTime() / 1000);
		let obj = {
			appid: appid,
			timeStamp: timeStamp
		};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign
			},obj);	
				rs.getRequests("getCountNum", params, (res) => {
					let {data}=res;
					if(data.code==200){
						let {fullPrice,rule}=data.data;
						
						for(let i=rule.length-1;i>=0;i--){
							if(fullPrice>=rule[i].price){
							this.ruleKind=i;
							return;	
							}else{
								this.ruleKind=-1;
							}
								
						}
					}
				})
	}
	},
	onShow() {
		this.openCart();
	}
}
